import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

import AppModal from '../Common/AppModal/AppModal'

import UserContent from '../Game/Role/User/UserContent'
import AngleContent from '../Game/Role/Enemy/Nobody/AngleContent'
import RectangleContent from '../Game/Role/Enemy/Nobody/RectangleContent'
import BossContent from '../Game/Role/Enemy/Boss/BossContent'

const styles = theme => ({
  root: {
  },
  title: {
    letterSpacing: '1rem'
  },
  keyboardBtn: {
    display: 'inline-block',
    margin: '.5rem',
    padding: '.5rem 1rem',
    borderRadius: '5px',
    background: '#fff',
    color: '#000'
  },
  pointer: {
    cursor: 'pointer'
  },
  start: {
    position: 'fixed',
    right: '.5rem',
    bottom: '1rem',
    background: 'darkred'
  },
  rank: {
    position: 'fixed',
    right: '.5rem',
    bottom: '5rem',
    cursor: 'pointer',
    background: 'orange'
  },
  whiteText: {
    color: '#fff'
  },
  toolbar: theme.mixins.toolbar
})

class ReadmeContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  handleChange = (event) => {
    const {updatePlayer} = this.props
    if (event.target.value) {
      updatePlayer(event.target.value)
    }
  }

  render () {
    const {classes, isModalOpen, toggleModal, player} = this.props

    return (
      <div className={classes.root}>
        <div className={classes.toolbar} />
        <Typography
          variant='display1'
          align='center'
          className={classes.whiteText}
          gutterBottom>
          Mission 殲滅全部敵人
        </Typography>
        <br /><br />
        <Grid container justify='center'>
          <Grid item xs={8} container alignItems='center'>
            <Grid item xs={2}>
              <UserContent isStatic />
            </Grid>
            <Grid item xs={10}>
              <Typography
                variant='title'
                className={classes.whiteText}
                gutterBottom>
                玩家控制砲台
              </Typography>
              <Typography
                variant='subheading'
                className={classes.whiteText}
                gutterBottom>
                使用
                <span className={classes.keyboardBtn}>左</span>
                <span className={classes.keyboardBtn}>右</span>
                方向鍵控制砲台角度，
                <span className={classes.keyboardBtn}>Space</span>
                發射子彈攻擊敵人
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <AngleContent isStatic />
            </Grid>
            <Grid item xs={10}>
              <Typography
                variant='title'
                className={classes.whiteText}
                gutterBottom>
                敵人 A
              </Typography>
              <Typography
                variant='subheading'
                className={classes.whiteText}
                gutterBottom>
                1到5秒內會隨機產生1隻的雜魚，擊墜需1下
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <RectangleContent isStatic />
            </Grid>
            <Grid item xs={10}>
              <Typography
                variant='title'
                className={classes.whiteText}
                gutterBottom>
                敵人 B
              </Typography>
              <Typography
                variant='subheading'
                className={classes.whiteText}
                gutterBottom>
                1到5秒內會隨機產生1隻的雜魚，擊墜需2下
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <BossContent isStatic />
            </Grid>
            <Grid item xs={10}>
              <Typography
                variant='title'
                className={classes.whiteText}
                gutterBottom>
                敵人 C
              </Typography>
              <Typography
                variant='subheading'
                className={classes.whiteText}
                gutterBottom>
                每擊墜5隻就會產生一隻，繞著軌道攻擊砲台，擊墜需3下
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <span
          className={`${classes.keyboardBtn} ${classes.start} ${classes.whiteText} ${classes.pointer}`}
          onClick={toggleModal}>
          開始殲滅敵人
        </span>
        <Link to='/rank'>
          <span className={`${classes.keyboardBtn} ${classes.rank} ${classes.whiteText} ${classes.pointer}`}>
            查看擊墜排行
          </span>
        </Link>
        <AppModal
          isModalOpen={isModalOpen}
          toggleModal={toggleModal}
          handleChange={this.handleChange}
          player={player} />
      </div>
    )
  }
}

export default withStyles(styles)(ReadmeContent)



